import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";


import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { validateTaxCreation } from '../../validation/tax';
import { createTax } from '../../api/misc';
import { validateAccountTypeCreation } from '../../validation/account';
import { createAccountType } from '../../api/accounting/account';

const Create = (props) => {

    const [submitting, setSubmitting] = React.useState(false)
    const [accountTypeData, setAccountTypeData] = React.useState({
        name: '',
        header: '',
    })
    const [errors, setErrors] = React.useState({});


    const _accountTypeDataChange = (e) => {
        setErrors({ ...errors, [e.target.name]: '' })
        setAccountTypeData({
            ...accountTypeData,
            [e.target.name]: e.target.value
        })
    }


    const _submit = () => {

        const { isValid, errors } = validateAccountTypeCreation(accountTypeData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };

        console.log('no errros');

        setSubmitting(true);
        //if no errros api call
        const requestJson = {
            "action": "add",
            "header": accountTypeData.header,
            "name": accountTypeData.name,
        }
        console.log(requestJson);
        createAccountType(requestJson)
            .then((res) => {
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Account type created successfully!', background: '#dffff0' })
                    try { props.history.push('/account-types') } catch (err) {
                        console.log(res.json.account_types[0]);
                        const createdInstance = res.json.account_types[0]
                        props.setCreatedToSelected({
                            value: createdInstance.id,
                            label: createdInstance.name
                        });
                        props.onHide();
                    }
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }
    const _cancel = () => {
        try { props.history.push('/account-types') } catch (err) {
            props.onHide();
        }
    }

    console.log(accountTypeData)

    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Account Type</h5>
                            <hr />
                            <Row>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Account Type Name*</Form.Label>
                                        <Form.Control name="name" value={accountTypeData.name} onChange={_accountTypeDataChange} type="text" placeholder="Type Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Header*</Form.Label>
                                        <Form.Control name="header" value={accountTypeData.header} onChange={_accountTypeDataChange} as="select">

                                            <option value='' disabled >Select...</option>
                                            <option value='assets'  >Assets</option>
                                            <option value='liabilities'  >Liabilities</option>
                                            <option value='expense'  >Expense</option>
                                            <option value='revenue'  >Revenue</option>
                                            <option value='draw'  >Draw</option>
                                            <option value='equity'  >Equity</option>
                                        </Form.Control>
                                        <ErrorBody>{errors.header}</ErrorBody>
                                    </Form.Group>
                                </Col>

                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={_cancel}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}

export default Create;
